:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: .25s;
}

* {
  box-sizing: border-box;
}

a:link, a:visited, a:hover, a:active {
  text-decoration: none;
}

html {
  scroll-behavior: smooth;
  background-color: #020617;
}

p {
  margin: 0;
}

button {
  all: unset;
  cursor: pointer;
}

body {
  color: #f3f4f6;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  min-height: 100vh;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.container {
  max-width: 1158px;
  margin: auto;
  padding: 0 9px;
}

.logo {
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  gap: 16px;
  font-size: 32px;
  font-weight: 700;
  display: flex;
}

.logo__image {
  width: 40px;
  height: 40px;
}

.nav_container {
  z-index: 100;
  filter: drop-shadow(0 1px 3px #0003);
  background-color: #020617;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

.nav {
  justify-content: space-between;
  align-items: center;
  max-width: 1158px;
  margin: auto;
  padding: 16px 9px;
  display: flex;
}

.search-form {
  font: inherit;
  justify-content: center;
  gap: 16px;
  width: 50%;
  display: flex;
}

.input-box {
  background-color: #111827;
  border: 1px solid #1f2937;
  border-radius: 4px;
  width: 100%;
  padding: 4px 16px;
}

.input {
  all: unset;
  color: #f3f4f6;
  width: 100%;
  padding: 4px 0;
}

.input::placeholder {
  color: #4b5563;
  opacity: 1;
}

.search-button {
  color: #f3f4f6;
  cursor: pointer;
  background-color: #4338ca;
  border: 1px solid #1f2937;
  border-radius: 4px;
  padding: 3px 12px;
  font-weight: 500;
}

.search-button:hover {
  background-color: #4f46e5;
}

.tag-list {
  flex-wrap: wrap;
  gap: 16px;
  max-width: 1140px;
  margin: 80px auto 0;
  display: flex;
}

.tag-list__item {
  color: #fff;
  background-color: #111827;
  border: 1px solid #1f2937;
  border-radius: 2px;
  font-size: 18px;
  font-weight: 500;
  display: inline-block;
}

.tag-list__item:hover {
  cursor: pointer;
  background-color: #4f46e5;
}

.tag-button {
  padding: 8px 16px;
}

.active {
  background-color: #4f46e5;
}

.gallery {
  grid-template-columns: repeat(4, minmax(240px, 1fr));
  justify-content: center;
  gap: 16px;
  max-width: 1140px;
  margin: 16px auto 0;
  display: grid;
}

.gallery__item {
  flex-direction: column;
  display: flex;
  box-shadow: 0 1px 3px #0003, 0 1px 1px #00000024, 0 2px 1px -1px #0000001f;
}

.gallery__image:hover {
  opacity: .8;
}

.image-wrapper {
  background: #1f2937;
  width: 100%;
  height: 0;
  padding-bottom: 80%;
  position: relative;
}

.gallery__image {
  opacity: 1;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
  width: 100%;
  height: 100%;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}

.info {
  color: #9ca3af;
  justify-content: space-around;
  align-items: center;
  margin-top: 8px;
  font-size: 12px;
  font-weight: 500;
  display: flex;
}

.info-item {
  decoration: none;
  align-items: center;
  gap: 8px;
  display: flex;
}

.info_icon {
  width: 18px;
}

.load-container {
  background-color: #111827;
  justify-content: center;
  align-items: center;
  max-width: 1140px;
  height: 100%;
  margin: 32px auto;
  display: flex;
}

.load-more {
  padding: 16px;
  font-size: 18px;
  font-weight: 700;
}

.load-more:hover {
  color: #4f46e5;
  text-decoration: underline;
}

.sl-overlay {
  color: red !important;
  background-color: #020617 !important;
}

.sl-wrapper .sl-close, .sl-wrapper .sl-navigation button.sl-next, .sl-wrapper .sl-navigation button.sl-prev, .sl-wrapper .sl-counter {
  color: #fff !important;
}

@media screen and (max-width: 550px) {
  .logo {
    clip: rect(1px 1px 1px 1px);
    clip: rect(1px, 1px, 1px, 1px);
    overflow: hidden;
    border: 0 !important;
    width: 1px !important;
    height: 1px !important;
    padding: 0 !important;
    position: absolute !important;
  }

  .search-form {
    width: 100%;
  }

  .gallery {
    grid-template-columns: repeat(1, minmax(0, 1fr));
  }
}

@media screen and (min-width: 550px) and (max-width: 767px) {
  .logo {
    font-size: 28px;
  }

  .logo__image {
    width: 34px;
    height: 34px;
  }

  .gallery {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
}

@media screen and (min-width: 768px) and (max-width: 1023px) {
  .search-form {
    width: 80%;
    margin-left: 16px;
  }

  .gallery {
    grid-template-columns: repeat(3, minmax(0, 1fr));
  }
}

@media screen and (min-width: 1024px) and (max-width: 1153px) {
  .search-form {
    width: 60%;
  }

  .gallery {
    grid-template-columns: repeat(3, minmax(0, 1fr));
  }
}

@media screen and (min-width: 1154px) {
  .tag-list {
    justify-content: space-between;
  }
}

